* {
  box-sizing: border-box;
}

 body {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
}

#root, .App {
  width: 100%;
  height: 100%;
}

canvas {
  max-width: 100%;
  max-height: 100%;
}

input {
  margin-left: 2em !important;
}

input[type="checkbox"] {
  margin: 0;
}

kbd {
  background-color: #eee;
  border-radius: 3px;
  border: 1px solid #b4b4b4;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2), 0 2px 0 0 rgba(255, 255, 255, .7) inset;
  color: #333;
  display: inline-block;
  font-size: .85em;
  font-weight: 700;
  line-height: 1;
  padding: 2px 4px;
  white-space: nowrap;
}

button {
  all: unset;
  margin: 0;
  padding: 1px 3px;
  font-size: larger;
  vertical-align: middle;
}

button:hover {
  background: #ddd;
}

button:active {
  background: #ccc;
}
